import {extend} from 'flarum/common/extend';
import app from 'flarum/admin/app';
import PermissionGrid from 'flarum/admin/components/PermissionGrid';
import settings from './settings';

const translationPrefix = 'kilowhat-formulaire.admin.permissions.';

app.initializers.add('kilowhat-formulaire', () => {
    app.extensionData
        .for('kilowhat-formulaire')
        .registerSetting(settings)
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'fill'),
            permission: 'formulaire.fill',
            allowGuest: true,
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'seeOwnUser'),
            permission: 'formulaire.seeOwnUser',
            allowGuest: true,
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'seeAnyUser'),
            permission: 'formulaire.seeAnyUser',
            allowGuest: true,
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'editOwnUser'),
            permission: 'formulaire.editOwnUser',
            allowGuest: true,
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'editAnyUser'),
            permission: 'formulaire.editAnyUser',
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'seeOwnDiscussion'),
            permission: 'discussion.seeOwnFormulaire',
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'seeAnyDiscussion'),
            permission: 'discussion.seeAnyFormulaire',
            allowGuest: true,
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'editOwnDiscussion'),
            permission: 'discussion.editOwnFormulaire',
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'editAnyDiscussion'),
            permission: 'discussion.editAnyFormulaire',
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'create'),
            permission: 'formulaire.create',
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'moderate'),
            permission: 'formulaire.moderate',
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'export'),
            permission: 'formulaire.export',
        }, 'formulaire')
        .registerPermission({
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'exportUserDetails'),
            permission: 'formulaire.exportUserDetails',
        }, 'formulaire')
        .registerPermission({
            // listForms allows listing all forms that are not deleted, even if you might not be able to see them
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'listForms'),
            permission: 'formulaire.listForms',
            allowGuest: true,
        }, 'formulaireAdvanced')
        .registerPermission({
            // listSubmissions allows listing all submissions that you have access to
            icon: 'fas fa-poll-h',
            label: app.translator.trans(translationPrefix + 'listSubmissions'),
            permission: 'formulaire.listSubmissions',
        }, 'formulaireAdvanced');

    extend(PermissionGrid.prototype, 'permissionItems', function (permissionGroups) {
        // Support for ExtensionPermissionGrid, do not show Formulaire settings on other extension's pages
        // @ts-ignore extensionId not type-hinted
        if (this.extensionId && this.extensionId !== 'kilowhat-formulaire') {
            return;
        }

        permissionGroups.add('formulaire', {
            label: app.translator.trans(translationPrefix + 'heading'),
            children: app.extensionData.getAllExtensionPermissions('formulaire').toArray()
        });

        permissionGroups.add('formulaire-advanced', {
            label: app.translator.trans(translationPrefix + 'heading-advanced'),
            children: app.extensionData.getAllExtensionPermissions('formulaireAdvanced').toArray()
        });
    });
});
