import app from 'flarum/admin/app';
import Switch from 'flarum/common/components/Switch';
import ExtensionPage from 'flarum/admin/components/ExtensionPage';
import Select from 'flarum/common/components/Select';
import composerPageExtensionLink from '../common/helpers/composerPageExtensionLink';

const settingsPrefix = 'formulaire.';
const translationPrefix = 'kilowhat-formulaire.admin.settings.';

export default function settings(this: ExtensionPage) {
    return [
        m('.Form-group', [
            m('label', app.translator.trans(translationPrefix + 'max-file-size')),
            m('input.FormControl', {
                type: 'number',
                bidi: this.setting(settingsPrefix + 'maxFileSize', ''),
                placeholder: '2048',
            }),
        ]),
        m('.Form-group', [
            Switch.component({
                state: this.setting(settingsPrefix + 'historyControls')() === '1',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'historyControls')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'history-controls')),
        ]),
        m('.Form-group', [
            Switch.component({
                state: this.setting(settingsPrefix + 'sideNavOnSubmission')() === '1',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'sideNavOnSubmission')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'side-nav-on-submission')),
        ]),
        m('.Form-group', [
            m('label', app.translator.trans(translationPrefix + 'checkbox-style')),
            Select.component({
                value: this.setting(settingsPrefix + 'checkboxStyle')() || 'far-square',
                onchange: (value: string) => {
                    this.setting(settingsPrefix + 'checkboxStyle')(value);
                },
                options: {
                    'far-square': app.translator.trans(translationPrefix + 'style-options.far-square'),
                    'fas-square': app.translator.trans(translationPrefix + 'style-options.fas-square'),
                    'far-circle': app.translator.trans(translationPrefix + 'style-options.far-circle'),
                    'fas-circle': app.translator.trans(translationPrefix + 'style-options.fas-circle'),
                    switch: app.translator.trans(translationPrefix + 'style-options.switch'),
                    native: app.translator.trans(translationPrefix + 'style-options.native'),
                },
            }),
        ]),
        m('.Form-group', [
            m('label', app.translator.trans(translationPrefix + 'radio-style')),
            Select.component({
                value: this.setting(settingsPrefix + 'radioStyle')() || 'far-circle',
                onchange: (value: string) => {
                    this.setting(settingsPrefix + 'radioStyle')(value);
                },
                options: {
                    'far-circle': app.translator.trans(translationPrefix + 'style-options.far-circle'),
                    'fas-circle': app.translator.trans(translationPrefix + 'style-options.fas-circle'),
                    native: app.translator.trans(translationPrefix + 'style-options.native'),
                },
            }),
        ]),
        m('.Form-group', [
            m('label', app.translator.trans(translationPrefix + 'horizontal-layout')),
            Switch.component({
                state: this.setting(settingsPrefix + 'horizontalLayoutStandaloneEdit')() === '1',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'horizontalLayoutStandaloneEdit')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'horizontal-layout-locations.standaloneEdit')),
            Switch.component({
                state: this.setting(settingsPrefix + 'horizontalLayoutStandaloneView')() === '1',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'horizontalLayoutStandaloneView')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'horizontal-layout-locations.standaloneView')),
            Switch.component({
                state: this.setting(settingsPrefix + 'horizontalLayoutProfileEdit')() === '1',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'horizontalLayoutProfileEdit')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'horizontal-layout-locations.profileEdit')),
            Switch.component({
                state: this.setting(settingsPrefix + 'horizontalLayoutProfileView')() === '1',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'horizontalLayoutProfileView')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'horizontal-layout-locations.profileView')),
            Switch.component({
                state: this.setting(settingsPrefix + 'horizontalLayoutSignUp')() === '1',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'horizontalLayoutSignUp')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'horizontal-layout-locations.signUp')),
            Switch.component({
                state: this.setting(settingsPrefix + 'horizontalLayoutDiscussionEdit')() !== '0',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'horizontalLayoutDiscussionEdit')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'horizontal-layout-locations.discussionEdit')),
            Switch.component({
                state: this.setting(settingsPrefix + 'horizontalLayoutDiscussionView')() !== '0',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'horizontalLayoutDiscussionView')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'horizontal-layout-locations.discussionView')),
            Switch.component({
                state: this.setting(settingsPrefix + 'horizontalLayoutDiscussionComposer')() !== '0',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'horizontalLayoutDiscussionComposer')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'horizontal-layout-locations.discussionComposer')),
        ]),
        m('.Form-group', [
            Switch.component({
                state: this.setting(settingsPrefix + 'uniformComposerLayout')() === '1',
                onchange: (value: boolean) => {
                    this.setting(settingsPrefix + 'uniformComposerLayout')(value ? '1' : '0');
                },
            }, app.translator.trans(translationPrefix + 'uniformComposerLayout')),
            m('.helpText', app.translator.trans(translationPrefix + 'uniformComposerLayoutHelp', {
                a: composerPageExtensionLink(),
            })),
        ]),
    ];
}
